.headerContainer{
    position: sticky;
    top: 0;
    z-index: 10;
    background-color: white;
    &{
        -webkit-box-shadow: 0px 1px 0px 1px rgba(194,194,194,0.43);
        -moz-box-shadow: 0px 1px 0px 1px rgba(194,194,194,0.43);
        box-shadow: 0px 1px 0px 1px rgba(194,194,194,0.43);
        width: 100%;
    }

    & , .buttonContainer{
        display: flex;
    }
    
    &{
        justify-content: space-between;
    }
    
    .title{
        font-weight:800;
        font-size:1.5rem;
        margin-left:75px;
        margin-top:20px;
    }

    .buttonContainer{
        margin-top:7.5px;
        font-size:1.125rem;
        margin-right:75px;
        font-weight:400;
        cursor: pointer;

        .darkmode-button{
            cursor: pointer;
            font-weight:lighter;
            outline:none;
            border:none;
            background:rgb(0 0 0 /0);
            font-size:20px;
            margin-bottom:5px;
            margin-right:5px;

            &.darkButton{
                color: white;
                background:#2b3743;
            }
        }
    }

    &.dark{
        color: white;
        background:#2b3743;
        -webkit-box-shadow: 0px 3px 0px 1px rgba(29,42,51,1);
        -moz-box-shadow: 0px 3px 0px 1px rgba(29,42,51,1);
        box-shadow: 0px 3px 0px 1px rgba(29,42,51,1);
    }
    @media screen and (max-width:700px){
        & .title{
            margin-left:50px;
            font-size:1.25rem;
        }
        & .buttonContainer{
            margin-right:50px;
            font-size:1rem;
        }
    }
    @media screen and (max-width:475px){
        & .title{
            margin-left:25px;
            font-size:1rem;
        }
        & .buttonContainer{
            margin-right:25px;
            font-size:0.813rem;
        }
    }
    @media screen and (max-width:320px){
        & .title{
            margin-left:15px;
            font-size:.8rem;
        }
        & .buttonContainer{
            margin-right:15px;
            font-size:.75rem;

            & .darkmode-button{
                font-size:.938rem;
                margin-right:2px;
                margin-bottom:2.5px;
            }
        }
    }
}